import React from 'react';
import { Container, Row, Col } from 'react-bootstrap';

function Footer() {
  const randomPhrase = () => {
    const phrases = [
      "Let's get it!",
      "Let's go!",
      "More fire.",
      "Positive vibes.",
      "Good morning, sweet world!",
      "Harsh."
    ];

    let phrase = phrases[Math.ceil(Math.random() * phrases.length)];

    return phrase;
  }

  return (
    <footer className='mt-5' style={{width:'95%', margin:'auto'}}>
      <Container fluid='true'>
        <Row className='border-top justify-content-between p-3'>
          <Col className='p-0' md={3} sm={12}>
            {randomPhrase()}
          </Col>
          <Col className='p-0 d-flex justify-content-end' md={3}>
            Hi, I'm Daniel Vega.
          </Col>
        </Row>
      </Container>

    </footer>
  );
}

export default Footer;