import React from 'react';
import Hero from '../Components/Hero/Hero';
import Carousel from '../Components/Carousel/Carousel';
import ImageBanner from '../Components/ImageBanner/ImageBanner';

function HomePage(props) {
  return(
    <div>
      <Hero title={props.title} subTitle={props.subTitle} text={props.text} />
      <Carousel />
      <ImageBanner src={props.src} alt={props.alt}/>
      <Hero subTitle='Life long learner. Slow burner.' 
      text="I've been teaching myself computer science over the past couple of years, and then went and enrolled in the University of Texas Full-Stack Web Development course. Here are some of the things I've been learning ... "/>

      <ul className="subject-list">
        <li className="subject">JavaScript</li>
        <li className="subject">Node.js</li>
        <li className="subject">React</li>
        <li className="subject">HTML and CSS</li>
        <li className="subject">Java</li>
        <li className="subject">Bootstrap</li>
        <li className="subject">MySql and MongoDB</li>
        
      </ul>
    </div>
  )
};

export default HomePage;